/** 常用色值 **/
/** 常用字体大小 **/
.home-wrap {
  height: calc(72vh);
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px;
  color: #000;
}


.header {
  background: #001529;
  color: #fff;
  font-size: 16px;
}
.header-top {
  text-align: right;
  padding: 10px 15px;
  border-bottom: 1px solid gold;
}
.header-top span {
  margin-right: 10px;
}
.breadcrumb {
  padding: 10px 10px 5px 10px;
}
.breadcrumb .breadcrumb-title {
  text-align: center;
  position: relative;
}
.breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  left: 128px;
  top: 29px;
  border: 10px solid #001529;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid transparent;
}
.breadcrumb .weather {
  text-align: right;
}
.breadcrumb .weather span {
  margin: 0 5px;
}
.breadcrumb .weather span img {
  width: 30px;
  vertical-align: top;
}

.logo {
  line-height: 90px;
  padding-left: 20px;
  background: #001529;
}
.logo img {
  height: 35px;
  vertical-align: middle;
}
.logo h1 {
  color: #fff;
  font-size: 20px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 0 10px;
}

/** 常用色值 **/
/** 常用字体大小 **/
body {
  background: #f1f3f5;
  font-family: "Helvetica Neue For Number", "PingFang SC Light", "Heiti SC", "San Francisco", "Helvetica", Arial, sans-serif;
}
ul,
li {
  list-style: none;
}
.clearfix:after {
  content: " ";
  clear: both;
  display: block;
  visibility: hidden;
}
.container .nav-left {
  background-color: #001529;
  color: #ffffff;
  height: calc(100vh);
}
.container .main {
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto;
}
.container .content {
  position: relative;
  background: #fff;
  padding: 20px;
}
.content-wrap {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;
  margin-bottom: 10px;
}
.content-wrap .ant-table-wrapper {
  margin-left: -1px;
  margin-right: -2px;
}

.ant-form-item {
  margin-bottom: 5px;
}
.ant-form-item-children .account-detail {
  border: none;
}
.ant-form-item-children .account-detail:hover {
  border: none;
  outline: none;
}
.edit-input {
  border: none;
  outline: none;
}
.edit-input:focus {
  border: none !important;
  outline: none;
  box-shadow: none;
}

